:root {
    --accents-amethyst: #FFFF00;
    --accents-dodgerblue: #BFBFBF;
    --accents-turquoise: #008000;
    --bg-default: #485365;
    --code-background: #576273;
    --code-text: #FFA500;
    --ft: 'BitPotionExt';
    --ft-mono: 'BitPotionExt';
    --headings: #000000;
    --highlights-crimson: #FF4500;
    --highlights-pumpkin: #FFA500;
    --line-height-base: 1.5;
    --links-default: #FFA500;
    --links-hover: #FF0000;
    --links-visited: #FFFF00;
    --text-default: #00FFFF;
    --bg-muted: #008000;
    --bd-muted: #FFFF00;
    --bg-accent: #008000;
    --bg-subtle: #800080;
    --bd-radius: 4px;
    --border-radius: 3px;
    --table-header-bg: #008000;
    --table-header-text: #FFFF00;
    --table-border: #FFFF00;
    --btn-bg: #0000FF;
    --btn-text: #FFFF00;
    --btn-hover-bg: #FF0000;
    --btn-hover-text: #FFFF00;
}
html {
    box-sizing: border-box;
    margin: auto 0 auto 0;
}
*, *::before, *::after {
    box-sizing: inherit;
}
*[class*="logo" i] svg, img[src*="logo" i] {
    max-height: 70px;
    vertical-align: middle;
}
*[class*="social"] svg, *[class*="icon"] svg, svg[class*="icon"], img[class*="emoji"][src$=".svg"] {
    max-height: 30px;
    vertical-align: middle;
}
.comment-box {
    background-color: rgba(var(--accents-dodgerblue), 0.1);
    border: 1px dotted var(--accents-dodgerblue);
    border-radius: 0.1875rem;
    line-height: 1.25;
    margin: auto 0 auto 0;
    padding: 0.3125rem 1.25rem;
}
.header-container {
    align-items: center;
    display: inline-flex;
    justify-content: center;
    padding: 1rem;
    text-align: center;
}
.header-container .search-link {
    margin-top: 10px;
}
.header-container .search-link a {
    color: inherit;
    text-decoration: none;
}
.header-content {
    align-items: center;
    display: flex;
    gap: 10px;
}
.iframe-container {
    overflow: hidden;
    padding-top: 56.25%;
    position: relative;
    width: 100%;
}
.iframe-container iframe {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}
.cluster {
    rect {
        fill: var(--bg-muted);
        rx: 5px;
        ry: 5px;
        stroke: var(--bd-muted);
        stroke-width: 1px;
    }
    text {
        fill: var(--text-default);
        font-weight: 600;
    }
}
@media (prefers-color-scheme: dark) {
    .cluster rect {
        fill: var(--bg-muted);
        stroke: var(--bd-muted);
    }
    .edgeLabel {
        background-color: var(--bg-subtle);
    }
    .edgePath .path, .edgePath .arrowheadPath {
        stroke: var(--text-default);
    }
    .node rect, .node circle, .node ellipse, .node polygon {
        fill: var(--bg-accent);
        stroke: var(--text-default);
    }
    .node text, .edgeLabel, .cluster text {
        color: var(--text-default);
        fill: var(--text-default);
    }
}
.edgeLabel {
    background-color: var(--bg-subtle);
    border-radius: var(--bd-radius);
    color: var(--text-default);
    max-width: 100%;
    padding: 0.25em 0.5em;
    white-space: pre-wrap;
}
.label text {
    max-width: 100%;
    text-align: center;
    word-wrap: break-word;
}
.node {
    rect, circle, ellipse, polygon {
        fill: var(--bg-accent);
        stroke: var(--text-default);
        stroke-width: 1px;
    }
    text {
        fill: var(--text-default);
    }
}
@media print {
    .cluster rect {
        fill: #ffffff;
        stroke: #000000;
    }
    .cluster text, .node text {
        fill: #000000;
    }
    .edgeLabel {
        background-color: #ffffff;
        border: 1px solid #000000;
        color: #000000;
    }
    .edgePath .path, .edgePath .arrowheadPath {
        stroke: #000000;
    }
    .node rect, .node circle, .node ellipse, .node polygon {
        fill: #ffffff;
        stroke: #000000;
    }
}
.post-content-body {
    background-color: var(--bg-default);
    color: var(--text-default);
    font-family: var(--ft);
    line-height: var(--line-height-base);
    margin: auto 0 auto 0;
    max-width: 100vw;
    padding: 1em;
    text-align: left;
}
.post-heading .info {
    color: rgba(var(--text-default), 0.8);
}
.post-heading .tags {
    color: var(--accents-amethyst);
}
.post-heading a {
    color: var(--accents-dodgerblue);
    text-decoration: none;
}
.post-heading a:hover {
    text-decoration: underline;
}
.post-title {
    color: var(--links-default);
}
.post-wrapper {
    overflow-wrap: break-word;
    word-wrap: break-word;
}
.post-wrapper > * {
    margin: 0.5em;
}
.search-link {
    background-color: rgba(var(--accents-dodgerblue), 0.1);
    border: 1px dotted var(--accents-dodgerblue);
    border-radius: var(--bd-radius);
    display: inline-block;
    padding: 0.5em;
    transition: all 0.3s ease;
}
.search-link:hover, .search-link:focus {
    background-color: rgba(var(--accents-dodgerblue), 0.2);
    transform: scale(1.05);
}
.tagged-posts {
    li {
        margin-bottom: 0.5em;
    }
    time {
        color: rgba(var(--text-default), 0.7);
    }
    ul {
        list-style: none;
        padding-left: 0;
    }
}
.tag-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    padding: 0;
    li {
        margin: 0.25em;
    }
    a {
        background-color: rgba(var(--accents-dodgerblue), 0.1);
        border: 1px dotted var(--accents-dodgerblue);
        border-radius: var(--bd-radius);
        display: inline-block;
        padding: 0.5em;
        transition: all 0.3s ease;
        &:hover, &:focus {
            background-color: rgba(var(--accents-dodgerblue), 0.2);
            color: var(--links-visited);
            transform: scale(1.05);
        }
    }
}
.tag-page {
    margin: 1em auto;
    max-width: 100%;
    padding: 1em;
}
